	import share from '@/components/share';
	export default{
		components: {
			share
		},
		data() {
			return {
				specClass: 'none',
				specSelected:[],

				fruitInfoDetail:[{
                    description:"",
                    fruitId:"",
                    image:"",
                    image1:"",
                    image2:"",
                    price:"",
                    sales:"",
                    stock:"",
                    title:"",
				}],

				favorite: true,
				shareList: [],
				imgList: [
					{
						src: '/static/product/xq1.png'
					},
					{
						src: '/static/product/xq2.png'
					},
					{
						src: '/static/product/xq3.png'
					}
				],
				desc: `
					<div style="width:100%">
						<img style="width:100%;display:block;" src="/static/product/xq1.png" />
						<img style="width:100%;display:block;" src="/static/product/xq2.png" />
						<img style="width:100%;display:block;" src="/static/product/xq3.png" />
						<img style="width:100%;display:block;" src="/static/product/xq2.png" />
						<img style="width:100%;display:block;" src="/static/product/xq1.png" />
					</div>
				`,
				specList: [
					{
						id: 1,
						name: '尺寸',
					},
					{	
						id: 2,
						name: '颜色',
					},
				],
				specChildList: [
					{
						id: 1,
						pid: 1,
						name: 'XS',
					},
					{
						id: 2,
						pid: 1,
						name: 'S',
					},
					{
						id: 3,
						pid: 1,
						name: 'M',
					},
					{
						id: 4,
						pid: 1,
						name: 'L',
					},
					{
						id: 5,
						pid: 1,
						name: 'XL',
					},
					{
						id: 6,
						pid: 1,
						name: 'XXL',
					},
					{
						id: 7,
						pid: 2,
						name: '白色',
					},
					{
						id: 8,
						pid: 2,
						name: '珊瑚粉',
					},
					{
						id: 9,
						pid: 2,
						name: '草木绿',
					},
				]
			};
		},
		async onLoad(options){
			
			//接收传值,id里面放的是标题，因为测试数据并没写id 
			// 接受url: `/pages/product/product?id=${id}`
			let id = options.id;
			if(id){
				//this.$api.msg(`点击了${id}`);
				uni.request({
				    url: 'http://127.0.0.1:9999/getFruitInfoDetail', //仅为示例，并非真实接口地址。
				    data: {
				        id: id
				    },
				    header: {					
				        //自定义请求头信息
				    },
				    success: (res) => {
				        console.log(res.data.data);
				        console.log("-----")
                        var fruitInfoDetail = res.data.data;
                        this.fruitInfoDetail = fruitInfoDetail;
				        // this.swiperLength = carouselList.length;
				        // this.carouselList = carouselList;
				    },
					fail:(res) =>{
						console.log("--*--")
					}
				});
			}
			
			
			//规格 默认选中第一条
			this.specList.forEach(item=>{
				for(let cItem of this.specChildList){
					if(cItem.pid === item.id){
						this.$set(cItem, 'selected', true);
						this.specSelected.push(cItem);
						break; //forEach不能使用break
					}
				}
			})
			this.shareList = await this.$api.json('shareList');
		},
		methods:{
			//规格弹窗开关
			toggleSpec() {
				if(this.specClass === 'show'){
					this.specClass = 'hide';
					setTimeout(() => {
						this.specClass = 'none';
					}, 250);
				}else if(this.specClass === 'none'){
					this.specClass = 'show';
				}
			},
			//选择规格
			selectSpec(index, pid){
				let list = this.specChildList;
				list.forEach(item=>{
					if(item.pid === pid){
						this.$set(item, 'selected', false);
					}
				})

				this.$set(list[index], 'selected', true);
				//存储已选择
				/**
				 * 修复选择规格存储错误
				 * 将这几行代码替换即可
				 * 选择的规格存放在specSelected中
				 */
				this.specSelected = []; 
				list.forEach(item=>{ 
					if(item.selected === true){ 
						this.specSelected.push(item); 
					} 
				})
				
			},
			//分享
			share(){
				this.$refs.share.toggleMask();	
			},
			//收藏
			toFavorite(){
				this.favorite = !this.favorite;
			},
			buy(){
				uni.navigateTo({
					url: `/pages/order/createOrder`
				})
			},
			stopPrevent(){}
		},

	}
